<template>
   <div class="BxContentProf__ChangePassword">
      <div class="boxNotif">
         <v-snackbar
				v-model="snackbar"
				:color="NotifColor"
				:timeout="timeout"
				top
			>
					<p>{{NotifText}}</p>
				<!-- <v-btn dark flat @click="snackbar = false">
					Close
				</v-btn> -->
			</v-snackbar>
		</div>
      <div class="FormTitle">
         <p>Change Password</p>
      </div>
      <v-form ref="fmChangePass" class="BxContentProf__ChangePassword__fmChangePass" v-model="valid" lazy-validation>
         <div class="FormGroup">
            <v-label>New Password</v-label>
            <v-text-field
               v-model="newPassword"
               :rules="[rules.required, rules.min]"
               :type="show1 ? 'text' : 'password'"
               hint="At least 8 characters"
            ></v-text-field>
         </div>
         <div class="FormGroup">
            <v-label>Confirm New Password</v-label>
            <v-text-field
               v-model="confirmPassword"
               :rules="[rules.passMatch, rules.required, rules.min]"
               :type="show1 ? 'text' : 'password'"
               hint="At least 8 characters"
            ></v-text-field>
         </div>
         <div class="FormGroup">
            <v-label>Current Password</v-label>
            <v-text-field 
               v-model="currentPassword"
               :rules="[rules.required, rules.min]"
               :type="show1 ? 'text' : 'password'"
               hint="At least 8 characters"></v-text-field>
         </div>
         
      </v-form> 
      <span class="line"></span>
      <div class="FormGroupBtnFlat">
            <v-btn class="btnSqure" :disabled="!valid" @click="UpdatePassword">Update Password</v-btn>
      </div>
      <v-dialog v-model="dialogLoding" width="150" persistent content-class="PersonalLoading">
         <v-card>
            <v-card-text class="text-xs-center">
               <v-progress-circular
                  indeterminate
                  color="primary"
                  :size="70"
                  :width="7"
               ></v-progress-circular>
            </v-card-text>
         </v-card>
      </v-dialog>
   </div>
</template>

<script>
	export default{
		data () {
			return {
            dialogLoding: false,
            snackbar: false,
            timeout: 5000,
            show1: '',
            NotifColor:'',
            NotifText:'',
            valid: false,
            newPassword: '',
            confirmPassword: '',
            currentPassword: '',
            rules: {
					required: value => !!value || 'Required.',
					min: v => v.length >= 8 || 'Min 8 characters',
               passMatch: (v) => v == this.newPassword || 'New Password and Confirm Password must match'
            },
			}
      },
      methods:{
         UpdatePassword (){
           
            if (this.$refs.fmChangePass.validate()) {
                  const formPS ={
                  old_password: this.currentPassword,
                  new_password: this.newPassword,
                  confirm_password:this.confirmPassword
               }
               const ConvertFormpass = JSON.stringify(formPS)
               this.$store.dispatch('POST_UPDATE_PASSWORD', ConvertFormpass)
            }  
         }
      },

      computed: {
         CheckPS(){
            return this.$store.state.ChangePass
         }
      },
      watch: {
         CheckPS(val){
            if(val.status === 'OK'){
               this.NotifColor ='success'
               this.NotifText = 'Change Password Success'
               this.dialogLoding = true
               setTimeout(()=>{
                  this.dialogLoding = false
                  this.snackbar = true
                  this.$refs.fmChangePass.reset()
               },2000);
            }else if (val.message === 'Wrong old password.' && val.data.status === 'ERROR'){
               this.NotifColor ='error'
               this.NotifText = 'Wrong Current Password'
               this.dialogLoding = true
               setTimeout(()=>{
                  this.dialogLoding = false
                  this.snackbar = true
					},2000);
            }else if(val.data === 500){
               this.NotifColor ='error'
               this.NotifText = 'Please Try again later'
               this.dialogLoding = true
               setTimeout(()=>{
                  this.dialogLoding = false
                  this.snackbar = true
                  this.$refs.fmChangePass.reset()
					},2000);
            }
         },

         
      },
	}
</script>


