<template>
   <div class="BxContentProf__DynedProInfo" style="margin-top:30px;margin-bottom:30px;">
      <div class="boxNotif" v-if="CheckTimezones || CheckTimezones.data.success">
         <v-snackbar
				v-model="snackbar"
				:color="NotifColor"
				:timeout="timeout"
				top
			>
				<p>{{NotifText}}</p>
				<!-- <v-btn dark flat @click="snackbar = false">
					Close
				</v-btn> -->
			</v-snackbar>
		</div>
      <div class="FormTitle">
         <p>Dyned Pro Info & Timezone</p>
      </div>
      <v-form ref="fmDynedProInfo" class="BxContentProf__DynedProInfo__fmDynedProInfo" v-model="valid" lazy-validation>
         <div class="FormGroup notAll">
            <v-label>Dyned Pro ID</v-label>
            <v-text-field readonly disabled :value='dynedproid'></v-text-field>
         </div>

         <div class="FormGroup notAll">
            <v-label>Server</v-label>
            <v-text-field readonly disabled :value='dynedproserver'></v-text-field>
         </div>

         <div class="FormGroup">
            <v-label>Time Zone</v-label>
            <multiselect
    					v-model="timezone"
    					:options="timezonelist"
    					label="region"
    					track-by="region"
    					placeholder="Select Timezone"
    				>
    				</multiselect>
         </div>


      </v-form>
      <span class="line"></span>
      <div class="FormGroupBtnFlat" style="margin-bottom:20px;">
         <v-btn class="btnSqure" :disabled="!valid" @click="UpdateTimezones">Update Timezone</v-btn>
      </div>
      <v-dialog v-model="dialogLoding" width="150" persistent content-class="PersonalLoading">
         <v-card>
            <v-card-text class="text-xs-center">
               <v-progress-circular
                  indeterminate
                  color="primary"
                  :size="70"
                  :width="7"
               ></v-progress-circular>
            </v-card-text>
         </v-card>
      </v-dialog>
   </div>
</template>

<script>
   import Multiselect from 'vue-multiselect'

   export default {
      props:[
         'Additional',
      ],
      components: {
  	    Multiselect
  	  },
      data() {
         return {
            dialogLoding: false,
            snackbar: false,
            NotifColor:'',
            NotifText:'',
            timeout: 5000,
            servers: ['Indonesia', 'China', 'Thailand'],
            valid: false,
            filedRules: [
               v => !!v || 'Field is required',
            ],
            dynedproid:this.Additional.dyned_pro_id,
            dynedproserver:this.Additional.server_dyned_pro,
            timezone: { region: this.Additional.timezone },
            timezonelist: [
               {'region':'Pacific/Midway'},
               {'region':'US/Samoa'},
               {'region':'US/Hawaii'},
               {'region':'US/Alaska'},
               {'region':'US/Pacific'},
               {'region':'America/Tijuana'},
               {'region':'US/Arizona'},
               {'region':'US/Mountain'},
               {'region':'America/Chihuahua'},
               {'region':'America/Mazatlan'},
               {'region':'America/Mexico_City'},
               {'region':'America/Monterrey'},
               {'region':'Canada/Saskatchewan'},
               {'region':'US/Central'},
               {'region':'US/Eastern'},
               {'region':'US/East-Indiana'},
               {'region':'America/Bogota'},
               {'region':'America/Lima'},
               {'region':'America/Caracas'},
               {'region':'Canada/Atlantic'},
               {'region':'America/La_Paz'},
               {'region':'America/Santiago'},
               {'region':'Canada/Newfoundland'},
               {'region':'America/Buenos_Aires'},
               {'region':'Greenland'},
               {'region':'Atlantic/Stanley'},
               {'region':'Atlantic/Azores'},
               {'region':'Atlantic/Cape_Verde'},
               {'region':'Africa/Casablanca'},
               {'region':'Europe/Dublin'},
               {'region':'Europe/Lisbon'},
               {'region':'Europe/London'},
               {'region':'Africa/Monrovia'},
               {'region':'Europe/Amsterdam'},
               {'region':'Europe/Belgrade'},
               {'region':'Europe/Berlin'},
               {'region':'Europe/Bratislava'},
               {'region':'Europe/Brussels'},
               {'region':'Europe/Budapest'},
               {'region':'Europe/Copenhagen'},
               {'region':'Europe/Ljubljana'},
               {'region':'Europe/Madrid'},
               {'region':'Europe/Paris'},
               {'region':'Europe/Prague'},
               {'region':'Europe/Rome'},
               {'region':'Europe/Sarajevo'},
               {'region':'Europe/Skopje'},
               {'region':'Europe/Stockholm'},
               {'region':'Europe/Vienna'},
               {'region':'Europe/Warsaw'},
               {'region':'Europe/Zagreb'},
               {'region':'Europe/Athens'},
               {'region':'Europe/Bucharest'},
               {'region':'Africa/Cairo'},
               {'region':'Africa/Harare'},
               {'region':'Europe/Helsinki'},
               {'region':'Europe/Istanbul'},
               {'region':'Asia/Jerusalem'},
               {'region':'Europe/Kiev'},
               {'region':'Europe/Minsk'},
               {'region':'Europe/Riga'},
               {'region':'Europe/Sofia'},
               {'region':'Europe/Tallinn'},
               {'region':'Europe/Vilnius'},
               {'region':'Asia/Baghdad'},
               {'region':'Asia/Kuwait'},
               {'region':'Africa/Nairobi'},
               {'region':'Asia/Riyadh'},
               {'region':'Europe/Moscow'},
               {'region':'Asia/Tehran'},
               {'region':'Asia/Baku'},
               {'region':'Europe/Volgograd'},
               {'region':'Asia/Muscat'},
               {'region':'Asia/Tbilisi'},
               {'region':'Asia/Yerevan'},
               {'region':'Asia/Kabul'},
               {'region':'Asia/Karachi'},
               {'region':'Asia/Tashkent'},
               {'region':'Asia/Kolkata'},
               {'region':'Asia/Kathmandu'},
               {'region':'Asia/Yekaterinburg'},
               {'region':'Asia/Almaty'},
               {'region':'Asia/Dhaka'},
               {'region':'Asia/Novosibirsk'},
               {'region':'Asia/Bangkok'},
               {'region':'Asia/Jakarta'},
               {'region':'Asia/Krasnoyarsk'},
               {'region':'Asia/Chongqing'},
               {'region':'Asia/Hong_Kong'},
               {'region':'Asia/Kuala_Lumpur'},
               {'region':'Australia/Perth'},
               {'region':'Asia/Singapore'},
               {'region':'Asia/Taipei'},
               {'region':'Asia/Ulaanbaatar'},
               {'region':'Asia/Urumqi'},
               {'region':'Asia/Irkutsk'},
               {'region':'Asia/Seoul'},
               {'region':'Asia/Tokyo'},
               {'region':'Australia/Adelaide'},
               {'region':'Australia/Darwin'},
               {'region':'Asia/Yakutsk'},
               {'region':'Australia/Brisbane'},
               {'region':'Australia/Canberra'},
               {'region':'Pacific/Guam'},
               {'region':'Australia/Hobart'},
               {'region':'Australia/Melbourne'},
               {'region':'Pacific/Port_Moresby'},
               {'region':'Australia/Sydney'},
               {'region':'Asia/Vladivostok'},
               {'region':'Asia/Magadan'},
               {'region':'Pacific/Auckland'},
               {'region':'Pacific/Fiji'},
            ],
         }
      },
      methods: {
         UpdateTimezones() {
            if (this.$refs.fmDynedProInfo.validate()) {
               const TimezoneData = {
                  timezone: this.timezone.region
               }
               this.$store.dispatch('POST_UPDATE_TIMEZONE', TimezoneData)
            }
            this.dialogLoding = true
         },
      },
      computed: {
         CheckTimezones(){
            return this.Additional
         },
         CckLoadDtTmZone(){
            return this.$store.state.CckLoadDtTmZone
         }
      },

      watch: {
         CckLoadDtTmZone(val){
            if (!val) return

				if (val.status === 200){
					this.$store.dispatch("GET_PROFILEADDITIONAL")
					this.dialogLoding = false
					this.NotifColor='success'
					this.NotifText = 'Update Timezone is Successfully'
               this.snackbar = true

               setTimeout(() => {
                  window.location.reload(false)
               }, 2000);
				}
         }
      },
   }
</script>



