<template>
	<div class="BxContentProf__PersonalInfo">
		<div class="boxNotif" v-if="CheckAdditional.message !== null">
         <v-snackbar
				v-model="snackbar"
				:color="NotifColor"
				:timeout="timeout"
				top
			>
					<p>{{NotifText}}</p>
				<!-- <v-btn dark flat @click="snackbar = false">
					Close
				</v-btn> -->
			</v-snackbar>
		</div>
		<div class="FormTitle">
			<p>Additional Information</p>
		</div>
		<v-form ref="fmPersonalInfo" enctype="multipart/form-data" class="BxContentProf__PersonalInfo__bxfmPersonalInfo" v-model="valid" lazy-validation>

			<div class="FormGroup notAll" v-if="this.IsRoleId == 'coach'">
				<v-label>Coach Type</v-label>
				<v-text-field readonly disabled v-model='coachType'></v-text-field>
			</div>

			<div class="FormGroup notAll" v-if="this.IsRoleId == 'coach'">
				<v-label>Tokens Balance</v-label>
				<v-text-field readonly disabled v-model='token'></v-text-field>
			</div>

			<div class="FormGroup notAll" v-if="this.IsRoleId == 'coach'">
				<v-label>Certifications</v-label>
				<v-text-field readonly disabled :value='certifications'></v-text-field>
			</div>

			<div class="FormGroup notAll" v-if="this.IsRoleId == 'student'">
				<v-label>Tokens</v-label>
				<v-text-field readonly disabled v-model='token'></v-text-field>
			</div>

			<div class="FormGroup notAll" v-if="this.IsRoleId == 'student'">
				<v-label>Certifications Goals</v-label>
				<v-text-field readonly disabled :value='certifications'></v-text-field>
			</div>


			<div class="FormGroup">
				<v-label>City</v-label>
				<v-text-field :rules="filedRules"  v-model='city'></v-text-field>
			</div>

			<div class="FormGroup">
				<v-label>State/Province</v-label>
				<v-text-field  :rules="filedRules" v-model='state'></v-text-field>
			</div>

			<div class="FormGroup">
				<v-label>Address</v-label>
				<v-text-field :rules="filedRules" v-model='address'></v-text-field>
			</div>

			<div class="FormGroup">
				<v-label>Zip</v-label>
				<v-text-field :rules="filedRules" v-model='zip'></v-text-field>
			</div>

			<div class="FormGroup">
				<v-label>Likes</v-label>
				<v-text-field v-model='likes'></v-text-field>
			</div>

			<div class="FormGroup">
				<v-label>Hobbies</v-label>
				<v-text-field v-model='hobbies'></v-text-field>
			</div>

			<div class="FormGroup">
				<v-label>Dislikes</v-label>
				<v-text-field v-model='dislike'></v-text-field>
			</div>

			<div class="FormGroup">
				<v-label>User Language</v-label>
				<multiselect
					v-model="userLanguage"
					:options="listoflanguage"
					label="name"
					track-by="name"
					placeholder="Select Language"
				>
				</multiselect>
			</div>

		</v-form>
		<span class="line"></span>

		<div class="FormGroupBtnFlat">
			<v-btn class="btnSqure" :disabled="!valid"  @click="UpdatePersonalInfo">Update Additional Info</v-btn>
		</div>

		<v-dialog v-model="dialogLoding" width="150" persistent content-class="PersonalLoading">
         <v-card>
            <v-card-text class="text-xs-center">
               <v-progress-circular
                  indeterminate
                  color="primary"
                  :size="70"
                  :width="7"
               ></v-progress-circular>
            </v-card-text>
         </v-card>
      </v-dialog>

	</div>
</template>

<script>
	import ListofLanguage from '../../../staticjson/ListofLanuage.json';
	import ListofCountry from '../../../staticjson/ListofCountry.json';
	import moment from 'moment'
	import Multiselect from 'vue-multiselect'

	export default{
		props:[
			'Additional',
			'GET_TokenInfo',
			'IsRoleId'
		],
		components: {
	    Multiselect
	  },
		data () {
			return {
				dialogLoding: false,
				snackbar: false,
           	NotifColor:'',
            NotifText:'',
            timeout: 3000,
				listoflanguage: ListofLanguage,
				valid: false,
				filedRules: [
					v => !!v || 'Field is required',
				],
				items: ['Male', 'Female'],
				date: new Date().toISOString().substr(0, 10),
				menu1: false,
				email: this.Additional.email,
				fullname: this.Additional.fullname,
				nickname: this.Additional.nickname,
				gender: this.Additional.gender,
				birthday: this.Additional.date_of_birth ,
				listofcountry:ListofCountry,
				setCountry: null,
				country: this.Additional.country,
         	state: this.Additional.state,
				address: this.Additional.address,
				city: this.Additional.city,
				phone: this.Additional.phone,
				hobbies: this.Additional.student_hobby,
         	likes: this.Additional.student_like,
         	dislike: this.Additional.student_dislike,
				certifications: this.Additional.cert_studying,
				phone: this.Additional.phone,
				zip: this.Additional.zip,
				spokenLanguage: this.Additional.spoken_language,
				userLanguage: { name: this.Additional.spoken_language },
				token: this.GET_TokenInfo.data.token_amount,
				bio: this.Additional.short_bio,
				coachType: this.Additional.coach_type,
			}
		},
		computed: {
    		DateBirthFormated () {
      		return this.date ? moment(this.date).format('dddd , MMMM Do  -  YYYY') : ''
			},
			CheckAdditional(){
            return this.$store.state.DataAdditional ? this.$store.state.DataAdditional : null
			},
			CckLoadDataPrsinfo(){
				return this.$store.state.CckLoadDataPrsinfo
			}
		},
		methods: {
			UpdatePersonalInfo () {
				if (this.$refs.fmPersonalInfo.validate()) {
					const formData = new FormData()
					formData.append('fullname', this.fullname)
					formData.append('nickname', this.nickname)
					formData.append('date_of_birth', this.date)
					formData.append('phone', this.phone)
					formData.append('gender', this.gender)
					formData.append('country', this.country)
					formData.append('city', this.city)
					formData.append('state', this.state)
					formData.append('address', this.address)
					formData.append('zip', this.zip)
					formData.append('student_like', this.likes)
					formData.append('student_dislike', this.dislike)
					formData.append('student_hobby', this.hobbies)
					formData.append('short_bio', this.bio)
					formData.append('user_language', this.userLanguage.name)
					formData.append('spoken_language', this.spokenLanguage)
					formData.append('_method', 'PATCH')

					this.$store.dispatch('POST_UPDATE_PERSONAL_INFO',formData)
					// console.log(formData);
				}
				this.dialogLoding = true
			}
		},
		mounted() {

			//SET VALUE Birthdate
			const TheBirthdate = new Date(this.birthday).toISOString().substr(0, 10)
			this.date = TheBirthdate
		},
		watch: {
			CckLoadDataPrsinfo(val){
				if (!val) return

				if (val.status === 200){
					this.$store.dispatch("GET_PROFILEADDITIONAL")
					this.dialogLoding = false
					this.NotifColor='success'
					this.NotifText = 'Update Additional is Successfully'
					this.snackbar = true
				}
			},
			Additional (val) {
				const data = val.data
				if (data.student_hobby == null) {
					return this.hobbies = "-"
				} else {
					return this.hobbies = data.student_hobby
				}
				if (data.student_like == null) {
					return this.hobbies = "-"
				} else {
					return this.hobbies = data.student_like
				}
				if (data.student_dislike == null) {
					return this.hobbies = "-"
				} else {
					return this.hobbies = data.student_dislike
				}
			}
		},
	}
</script>
<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>



